import React, {Component} from 'react';
import toast from '../toast';

export default class ReportWaiting extends Component {

  constructor(props) {
    super(props);
    this.state = {
      sortByType: {
        sortBy: 0,    //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortByIdReporter: true,
        sortByIdAccused: true,
        sortById: true,
        sortByTime: true,
        sortByDone: true,
      },
      listReport: [
        {
          stt: 2,
          reporter: {
            id: "143123",
            name: "con heo"
          },
          accused: {
            id: "313312",
            name: "con chó"
          },
          detail: "con chó rượt con heo",
          timeCreate: "1-2-2018 14h14m14s",
          done: false,
        },
        {
          stt: 3,
          reporter: {
            id: "6523",
            name: "con heo cái"
          },
          accused: {
            id: "123456213",
            name: "con heo đực"
          },
          detail: "con heo cái giành ăn của con heo đực",
          timeCreate: "3-1-2018 14h50m14s",
          done: false,
        },
      ]
    }
  }

  doneReport(elm, i) {
    const report = this.state.listReport[i];
    if (window.confirm("Bạn muốn hoàn thành bản report này?")) {
      report.done = true;
      toast.success("Bạn vừa hoàn thành 1 report");
      this.setState({
        listReport: this.state.listReport,
      })
    }
  }


  setSortByIdReporter() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortByIdReporter = !sortBy.sortByIdReporter;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByIdAccused() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortByIdAccused = !sortBy.sortByIdAccused;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByTime() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortByTime = !sortBy.sortByTime;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByDone() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 2;
    sortBy.sortByDone = !sortBy.sortByDone;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }


  render() {
    return (
      <div className="report-waiting">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title">
              <span>
                Danh sách report người dùng
              </span>
            </div>
            <div className="block-content-body">
              <table className="table table-hover table-bordered">
                <thead className="thead-dark">
                <tr className="">
                  <th onClick={() => this.setSortByIdReporter()}>Người tạo
                    <span className="float-right">
                    <i className={"fas " + ((this.state.sortByType.sortByIdReporter) ? "fa-sort-down" : "fa-sort-up")}></i>
                  </span>
                  </th>
                  <th onClick={() => this.setSortByIdAccused()}>Đối tượng
                    <span className="float-right">
                    <i className={"fas " + ((this.state.sortByType.sortByIdAccused) ? "fa-sort-down" : "fa-sort-up")}></i>
                  </span>
                  </th>
                  <th>Chi tiết</th>
                  <th onClick={() => this.setSortByTime()}>Thời gian
                    <span className="float-right">
                    <i className={"fas " + ((this.state.sortByType.sortByTime) ? "fa-sort-down" : "fa-sort-up")}></i>
                  </span>
                  </th>
                  <th className="text-center" onClick={() => this.setSortByDone()}>Đã xử lý
                    <span className="float-right">
                    <i className={"fas " + ((this.state.sortByType.sortByDone) ? "fa-sort-down" : "fa-sort-up")}></i>
                  </span>
                  </th>
                </tr>
                </thead>
                <tbody>
                {
                  this.state.listReport.map((elm, i) => (
                    <tr key={i} className={elm.done?"reportDone":null} onClick={elm.done?null:(() => this.doneReport(elm, i))}>
                      <td>
                        <p className="mb-1"><b>Tên: </b>{elm.reporter.name}</p>
                        <p className="mb-1"><b>Id: {'\u00A0'}{'\u00A0'}{'\u00A0'}</b>{elm.reporter.id}</p>
                      </td>
                      <td>
                        <p className="mb-1"><b>Tên: </b>{elm.accused.name}</p>
                        <p className="mb-1"><b>Id: {'\u00A0'}{'\u00A0'}{'\u00A0'}</b>{elm.accused.id}</p>
                      </td>
                      <td>
                        <p>
                          {elm.detail}
                        </p>
                      </td>
                      <td>
                        <p>
                          {elm.timeCreate}
                        </p>
                      </td>
                      <td className="text-center processing">
                        {
                          elm.done?
                            (
                              <span className="reportDone text-dark">DONE</span>
                            ):
                            (
                              <span className="btn"><i className="far fa-clock"></i></span>
                            )
                        }
                      </td>
                    </tr>
                  ))
                }
                </tbody>

              </table>

            </div>
          </div>
        </div>
      </div>
    );
  };

}


