import { CHANGE_VIEW } from "../actions/nav";

const initState = {
  id: 'list-all',
};
export default (state = initState, action) => {
  switch(action.type) {
    case "@@redux/INIT":
      return initState;
    case CHANGE_VIEW:
      return Object.assign({}, state, { id: action.id });
    default:
      return state;
  }
}





