import React, {Component} from 'react';

export default class ListAll extends Component {


  constructor(props) {
    super(props);
    this.state = {
      popup: {
        elm: 0,
        content: "detail",
      },
      sortByType: {
        sortBy: 0,    //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortIdUp: true,
        sortNameAToZ: true,
        sortStatusLock: true,
        sortStatusTick: true,
      },
      userListAll: [
        {
          id: "id con gà",
          name: "Một con gà",
          detail: "có 2 chân",
          lock: false,
          tick: false,
        },
        {
          id: "id con heo",
          name: "Một con heo",
          detail: "có 4 chân",
          lock: false,
          tick: true,
        },
        {
          id: "id con chó",
          name: "Một con chó",
          detail: "có 4 chân",
          lock: true,
          tick: false,
        },
        {
          id: "id con mèo",
          name: "Một con mèo",
          detail: "có 4 chân",
          lock: true,
          tick: true,
        },
      ]
    }
  }


  detailClick(e, i) {
    e.stopPropagation();
    this.setState({
      popup: {
        elm: i,
        content: "detail",
      }
    });
  }

  postClick(e, i) {
    e.stopPropagation();
    this.setState({
      popup: {
        elm: i,
        content: "post",
      }
    });
  }

  msgClick(e, i) {
    e.stopPropagation();
    this.setState({
      popup: {
        elm: i,
        content: "msg",
      }
    });
  }

  setSortById() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortIdUp = !sortBy.sortIdUp;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByName() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortNameAToZ = !sortBy.sortNameAToZ;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByLock() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 2;
    sortBy.sortStatusLock = !sortBy.sortStatusLock;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  setSortByTick() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 3;
    sortBy.sortStatusTick = !sortBy.sortStatusTick;
    this.setState({
      sortByType: this.state.sortByType,
    })
  }

  toggle_tick_account(i) {
    const user = this.state.userListAll[i];
    user.tick = !user.tick;
    this.setState({
      userListAll: this.state.userListAll,
    });
  };

  toggle_lock_account(e, i) {
    e.stopPropagation();
    const user = this.state.userListAll[i];
    user.lock = !user.lock;
    var x = window.confirm(((user.lock) ? ("Bạn muốn khóa tài khoản: ") : ("Bạn muốn mở khóa tài khoản: ")) + user.id + " ?");
    if (x) {
      this.setState({
        userListAll: this.state.userListAll,
      });
    }
  };
  renderPopUp() {
    const { popup, userListAll } = this.state;
    switch (popup.content) {
      case "detail":
        return ("Chi tiết bác sĩ " + (userListAll[popup.elm].name));
      case "post":
        return ("Danh sách bài viết của " + (userListAll[popup.elm].name));
      case "msg":
        return ("Gửi tin nhắn cho " + (userListAll[popup.elm].name));
      default:
        return "Error";
    }
  }
  render() {
    return (
      <div className="list-all">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title d-flex justify-content-between">
              <span className="float-left">
                Tất cả
              </span>
              <div className="col-right">
              <span className="h-100 target-content-user mr-2">
                <input type="text" name="search" placeholder="Search.." className="p-0 pl-2 m-0" />
              </span>
                <span className="h-100">
                <select name="" id="">
                  <option value="">Chọn thao tác</option>
                  <option value="">Gửi tin nhắn</option>
                  <option value="">Khóa</option>
                  <option value="">Mở khóa</option>
                </select>
              </span>
              </div>
            </div>
            <div className="block-content-body">
              <div className="list-user position-relative">


                <div className="popup modal fade" id="list-popup" tabIndex="-1" role="dialog"
                  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                  <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">

                      <div className="modal-header">
                        <h5 className="modal-title" id="list-popup-title">
                          {
                            this.renderPopUp()
                          }
                        </h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                      <div className="modal-body">
                        ...
                      </div>
                      <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                      </div>

                    </div>
                  </div>
                </div>


                {/*<div className="popup modal fade" id="list-popup" >*/}

                {/*<div className={"detail-popup "+(this.state.popup.detail?"d-block":"d-none")}>*/}
                {/*<div className="popup-content m-auto bg-light position-relative">*/}
                {/*<button type="button" className="close" data-dismiss="modal" aria-label="Close">*/}
                {/*<span aria-hidden="true">&times;</span>*/}
                {/*</button>*/}
                {/*</div>*/}
                {/*</div>*/}

                {/*<div className={"post-popup "+(this.state.popup.post?"d-block":"d-none")}>*/}
                {/*<div className="popup-content m-auto bg-light position-relative">*/}

                {/*</div>*/}
                {/*</div>*/}

                {/*<div className={"msg-popup "+(this.state.popup.msg?"d-block":"d-none")}>*/}
                {/*<div className="popup-content m-auto bg-light position-relative">*/}

                {/*</div>*/}
                {/*</div>*/}
                {/*</div>*/}


                <table className="table table-hover">
                  <thead className="thead-dark">
                  <tr className="">
                    <th onClick={() => this.setSortById()}>Id
                      <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortIdUp) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th onClick={() => this.setSortByName()}>Họ và tên
                      <span className="float-right">
                        <i
                          className={"fas " + ((this.state.sortByType.sortNameAToZ) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th>Chi tiết</th>
                    <th></th>
                    <th className="text-center" onClick={() => this.setSortByLock()}>Khóa tài khoản
                      <span className="float-right">
                        <i
                          className={"fas " + ((this.state.sortByType.sortStatusLock) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                    <th className="text-center" onClick={() => this.setSortByTick()}>Đánh dấu
                      <span className="float-right">
                        <i
                          className={"fas " + ((this.state.sortByType.sortStatusTick) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                    </th>
                  </tr>
                  </thead>
                  <tbody>
                  {
                    this.state.userListAll.map((elm, i) => (
                      <tr key={i} onClick={() => this.toggle_tick_account(i)}>
                        <td>{elm.id}</td>
                        <td>{elm.name}</td>
                        <td>{elm.detail}</td>
                        <td className="d-flex justify-content-around text-center">
                          <span title="Chi tiết" onClick={(e) => this.detailClick(e, i)} data-toggle="modal"
                            data-target="#list-popup"><i className="fas fa-info-circle"></i></span>
                          <span title="Các bài viết" onClick={(e) => this.postClick(e, i)} data-toggle="modal"
                            data-target="#list-popup"><i className="fas fa-th-list"></i></span>
                          <span title="Gửi tin nhắn" onClick={(e) => this.msgClick(e, i)} data-toggle="modal"
                            data-target="#list-popup"><i className="far fa-envelope"></i></span>
                        </td>
                        <td className="text-center">
                          {
                            elm.lock ?
                              (
                                <span title="Tài khoản đang khóa" onClick={(e) => this.toggle_lock_account(e, i)}
                                  className="icon-lock">
                                  <i className="fas fa-lock" />
                                </span>
                              ) :
                              (
                                <span title="Tài khoản đang mở" onClick={(e) => this.toggle_lock_account(e, i)}
                                  className="icon-unlock">
                                  <i className="fas fa-lock-open" />
                                </span>
                              )
                          }
                        </td>
                        <td className="text-center">
                          <span><i
                            className={((elm.tick) ? "fas fa-check" : "text-secondary far fa-square")}></i></span>
                        </td>
                      </tr>
                    ))
                  }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

}


